.App{padding:0 5%;position:relative;z-index:10}.title{font-size:3rem;color:#fff;text-align:left}.card__item{width:calc(25% - 30px);margin:0 15px 30px;height:100vh;min-height:300px;max-height:200px;background:#000;background:linear-gradient(0deg, #232323, #3e3e3e);padding:3%;display:flex;align-items:center;border:0;transition:all .15s ease-in-out;position:relative;overflow:hidden;display:inline-block;float:left}.card__item.tocom{background:#0b4932;background:linear-gradient(0deg, #0b4932, #17a96a)}.card__item.amazon{background:#3aacd1;background:linear-gradient(0deg, #3aacd1, #000)}.card__item.hbo{background:#fff}.card__item.globoplay{background:#f91817}.card__item.globoplay img{-webkit-filter:brightness(20);filter:brightness(20)}.card__item p{position:absolute;bottom:-10px;left:0;background:linear-gradient(0deg, rgba(84,84,84,0.321569), transparent);text-shadow:rgba(40,43,52,0.3) 0 0 2px;font-weight:700;margin:0;padding:10px;width:94%;height:30px;color:white;font-size:15px;text-transform:uppercase;line-height:30px;opacity:0;transition:all .15s ease-in-out;transform:scale(1, 0)}.card__item:hover{outline:0;transform:scale(1.1);z-index:10}.card__item:hover p{opacity:1;bottom:0;transform:scale(1)}.card__logo{width:100%}*{margin:0px;padding:0px}body{font-family:'Exo', sans-serif}.context{width:100%;position:absolute;top:50vh}.context h1{text-align:center;color:#fff;font-size:50px}.area{width:100%;height:100vh;position:fixed}.circles{position:fixed;top:0;left:0;width:100%;height:100%;overflow:hidden}.circles li{position:absolute;display:block;list-style:none;width:20px;height:20px;background:rgba(255,255,255,0.2);-webkit-animation:animate 25s linear infinite;animation:animate 25s linear infinite;bottom:-150px}.circles li:nth-child(1){left:25%;width:80px;height:80px;-webkit-animation-delay:0s;animation-delay:0s}.circles li:nth-child(2){left:10%;width:20px;height:20px;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-duration:12s;animation-duration:12s}.circles li:nth-child(3){left:70%;width:20px;height:20px;-webkit-animation-delay:4s;animation-delay:4s}.circles li:nth-child(4){left:40%;width:60px;height:60px;-webkit-animation-delay:0s;animation-delay:0s;-webkit-animation-duration:18s;animation-duration:18s}.circles li:nth-child(5){left:65%;width:20px;height:20px;-webkit-animation-delay:0s;animation-delay:0s}.circles li:nth-child(6){left:75%;width:110px;height:110px;-webkit-animation-delay:3s;animation-delay:3s}.circles li:nth-child(7){left:35%;width:150px;height:150px;-webkit-animation-delay:7s;animation-delay:7s}.circles li:nth-child(8){left:50%;width:25px;height:25px;-webkit-animation-delay:15s;animation-delay:15s;-webkit-animation-duration:45s;animation-duration:45s}.circles li:nth-child(9){left:20%;width:15px;height:15px;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-duration:35s;animation-duration:35s}.circles li:nth-child(10){left:85%;width:150px;height:150px;-webkit-animation-delay:0s;animation-delay:0s;-webkit-animation-duration:11s;animation-duration:11s}@-webkit-keyframes animate{0%{transform:translateY(0) rotate(0deg);opacity:1;border-radius:0}100%{transform:translateY(-1000px) rotate(720deg);opacity:0;border-radius:50%}}@keyframes animate{0%{transform:translateY(0) rotate(0deg);opacity:1;border-radius:0}100%{transform:translateY(-1000px) rotate(720deg);opacity:0;border-radius:50%}}

