.App {
  padding: 0 5%;
  position: relative;
  z-index: 10;
}

.title {
  font-size: 3rem;
  color: #fff;
  text-align: left;
}
.card {
  // overflow: scroll;
  &__group {
    // display: flex;
    // width: 1720px;
    // overflow: scroll;
    // padding: 40px 0;
  }
  &__item {
    width: calc(25% - 30px);
    margin: 0 15px 30px;
    height: 100vh;
    min-height: 300px;
    max-height: 200px;
    background: #000;
    background: linear-gradient(0deg, #232323, #3e3e3e);
    padding: 3%;
    display: flex;
    align-items: center;
    border: 0;
    transition: all .15s ease-in-out;
    position: relative;
    overflow: hidden;
    display: inline-block;
    float: left;
    &.tocom {
      background: #0b4932;
      background: linear-gradient(0deg, #0b4932, #17a96a);
    }
    &.amazon {
      background: #3aacd1;
      background: linear-gradient(0deg, #3aacd1, #000);
    }
    &.hbo {
      background: #fff;
    }
    &.globoplay {
      background: #f91817;
      img {
        filter: brightness(20);
      }
    }

    p {
      position: absolute;
      bottom: -10px;
      left: 0;
      background: linear-gradient(0deg, #54545452, transparent);
      text-shadow: rgba(40, 43, 52, 0.3) 0 0 2px;
      font-weight: 700;
      margin: 0;
      padding: 10px;
      width: 94%;
      height: 30px;
      color: white;
      font-size: 15px;
      text-transform: uppercase;
      line-height: 30px;
      opacity: 0;
      transition: all .15s ease-in-out;
      transform: scale(1, 0);
    }

    &:hover {
      outline: 0;
      // box-shadow: rgba(0, 0, 0, 0.5) 0 0 30px;
      transform: scale(1.1);
      z-index: 10;

      p {
        opacity: 1;
        bottom: 0;
        transform: scale(1);
      }
    }
  }
  &__logo {
    width: 100%;
  }
}






@import url('https://fonts.googleapis.com/css?family=Exo:400,700');

*{
    margin: 0px;
    padding: 0px;
}

body{
    font-family: 'Exo', sans-serif;
}


.context {
    width: 100%;
    position: absolute;
    top:50vh;
    
}

.context h1{
    text-align: center;
    color: #fff;
    font-size: 50px;
}


.area{
    width: 100%;
    height:100vh;
    position: fixed;
}

.circles{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}